<template>
	<el-container>
		<el-header>
			<div class="ub ub-f1 ub-pc ub-ac top-bar" style="font-size:20px;color：yellow;">
				计算盈利
				<!--<div class="ub ub-ac ub-pc" style="font-size:20px;color：yellow;">
					计算盈利
				</div>-->

			</div>
		</el-header>
		<el-main>
			<div class="fly-table-wrapper">
				<el-row type="flex" align="middle" class="event-content">
					<el-col class="event-btn-col">
						<el-button icon="el-icon-plus" type="primary" @click="addDialog = true">添 加</el-button>
						<el-button icon="el-icon-delete" type="danger" @click="clearDialog = true">清除所有</el-button>
					</el-col>
				</el-row>
				<el-table show-summary border v-loading="loading" :data="tableData" header-cell-class-name="fly-table-title" style="width: 100%">
					<el-table-column label="序号" prop="order">
					</el-table-column>
					<el-table-column label="名称" prop="name">
					</el-table-column>
					<el-table-column align="center" label="收入(元)" prop="money">
					</el-table-column>
					<el-table-column align="center" label="百分比">
						<template slot-scope="scope">
							<el-select v-model="scope.row.percent" placeholder="请选择" @change="changePercent(scope.row)">
								<el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value">
								</el-option>
							</el-select>
						</template>
					</el-table-column>
					<el-table-column align="center" label="计算后(元)" prop="aftermoney">
					</el-table-column>
					<el-table-column label="操作">
						<template slot-scope="scope">
							<el-tooltip class="item" effect="dark" content="编辑" placement="top">
								<el-button size="mini" type="primary" circle icon="el-icon-edit" @click="handleEdit(scope.row)"></el-button>
							</el-tooltip>
							<el-tooltip class="item" effect="dark" content="删除" placement="top">
								<el-button size="mini" type="danger" circle icon="el-icon-delete" @click="handleDelete(scope.row)"></el-button>
							</el-tooltip>
							
						</template>
					</el-table-column>
				</el-table>
			</div>
		</el-main>
		<el-dialog title="添加" :visible.sync="addDialog" width="50%" custom-class="match-dialog" center>
			<el-form ref="addForm" :model="addForm" :rules="addFormRules" label-width="150px">
				<el-form-item class="marB20" label="序号：" prop="order" size="small" style="width:70%;">
					<el-input clearable placeholder="序号" v-model="addForm.order" autocomplete="off"></el-input>
				</el-form-item>
				<el-form-item class="marB20" label="姓名："  size="small" style="width:70%;">
					<el-input clearable placeholder="姓名" v-model="addForm.name" autocomplete="off"></el-input>
				</el-form-item>
				<el-form-item class="marB20" label="收入（元）：" prop="money" size="small" style="width:70%;">
					<el-input clearable placeholder="收入" v-model="addForm.money" autocomplete="off"></el-input>
				</el-form-item>
				<el-form-item class="marB20" label="百分比：" size="small" style="width:70%;">
					<el-select v-model="addForm.percent" placeholder="请选择">
						<el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value">
						</el-option>
					</el-select>
				</el-form-item>
			</el-form>
			<span slot="footer" class="ub ub-pe dialog-footer">
			    <el-button @click="addDialog = false">取 消</el-button>
			    <el-button type="primary" @click="validAdd('addForm')">确 定</el-button>
			</span>
		</el-dialog>
		<el-dialog title="修改" :visible.sync="changeDialog" width="50%" custom-class="match-dialog" center>
			<el-form ref="changeForm" :model="changeForm" :rules="addFormRules" label-width="150px">
				<el-form-item class="marB20" label="序号：" prop="order" size="small" style="width:70%;">
					<el-input clearable placeholder="序号" v-model="changeForm.order" autocomplete="off"></el-input>
				</el-form-item>
				<el-form-item class="marB20" label="姓名："  size="small" style="width:70%;">
					<el-input clearable placeholder="姓名" v-model="changeForm.name" autocomplete="off"></el-input>
				</el-form-item>
				<el-form-item class="marB20" label="收入（元）：" prop="money" size="small" style="width:70%;">
					<el-input clearable placeholder="收入" v-model="changeForm.money" autocomplete="off"></el-input>
				</el-form-item>
				<el-form-item class="marB20" label="百分比：" size="small" style="width:70%;">
					<el-select v-model="changeForm.percent" placeholder="请选择">
						<el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value">
						</el-option>
					</el-select>
				</el-form-item>
			</el-form>
			<span slot="footer" class="ub ub-pe dialog-footer">
			    <el-button @click="changeDialog = false">取 消</el-button>
			    <el-button type="primary" @click="validChange('changeForm')">确 定</el-button>
			</span>
		</el-dialog>
		<!--退出结束-->
		<el-dialog title="提示" :visible.sync="deleteDialog" width="30%" custom-class="exit-dialog" center>
			<span>确定删除吗？</span>
			<span slot="footer" class="dialog-footer">
			    <el-button @click="deleteDialog = false">取 消</el-button>
			    <el-button type="primary" @click="sureDelete">确 定</el-button>
			</span>
		</el-dialog>
		<!--退出结束-->
		<el-dialog title="提示" :visible.sync="clearDialog" width="30%" custom-class="exit-dialog" center>
			<span>确定清除所有吗？</span>
			<span slot="footer" class="dialog-footer">
			    <el-button @click="clearDialog = false">取 消</el-button>
			    <el-button type="primary" @click="sureClear">确 定</el-button>
			</span>
		</el-dialog>
	</el-container>
</template>

<script>
	function isNum(val) {
		var re = /^[0-9]+(.[0-9]{2})?$/;
		if(re.test(val)) {　　　　
			return true;　　
		}else {
			return false;
		}
	}
	export default {
		name: 'home',
		data() {
			var isNumber = (rule, value, callback) => {
				if(isNum(value) || value == '') {
					callback();
				} else {
					callback(new Error('请输入数字(小数保留两位)'));
				}
			};
			return {
				loading: true,
				tableData: [],
				options: [{
					value: 0.30,
					label: '30%'
				}, {
					value: 0.25,
					label: '25%'
				}, {
					value: 0.20,
					label: '20%'
				}, {
					value: 0.15,
					label: '15%'
				}, {
					value: 0.10,
					label: '10%'
				}, {
					value: 0.05,
					label: '5%'
				}],
				addDialog: false,
				addForm: {
					order: '',
					name: '',
					money: '',
					percent: 0.30,
				},
				changeForm: {
					id:'',
					order: '',
					name: '',
					money: '',
					percent:'',
				},
				changeDialog:false,
				deleteId:'',
				deleteDialog:false,
				clearDialog:false,
				addFormRules:{
					order:[
						{
							required: true,
							message: '请输入序号',
							trigger: 'blur'
						}
					],
					money:[
						{
							required: true,
							validator: isNumber,
							trigger: 'blur'
						},
					]
				}
			}
		},
		watch: {
			'addDialog': {
				handler(newVal, oldVal) {
					if(!newVal) {
						this.addForm = {
							order: '',
							name: '',
							money: '',
							percent: 0.30,
						};
						this.$refs.addForm.resetFields();
					}
				},
			}
		},
		mounted() {
			this.$nextTick(() => {
				this.get_data();
			})
		},
		methods: {
			validAdd(formName) {
				this.$refs[formName].validate((valid) => {
					if(valid) {
						this.add();
					} else {
						console.log('error submit!!');
						return false;
					}
				});
			},
			validChange(formName) {
				this.$refs[formName].validate((valid) => {
					if(valid) {
						this.change();
					} else {
						console.log('error submit!!');
						return false;
					}
				});
			},
			changePercent(row){
				console.log(row)
				let list = this.$getlocalStorage('listData')===''?[]:this.$getlocalStorage('listData');
				this.tableData = list.map(item=>{
					if(item.id===row.id){
						item.order = row.order;
						item.name = row.name;
						item.money = row.money;
						item.percent =row.percent;
						item.aftermoney = this.$accMul(item.money, item.percent).toFixed(2);
					}
					return item;
				})
				this.$setlocalStorage('listData',this.tableData);
			},
			add(){
				let list = this.$getlocalStorage('listData')===''?[]:this.$getlocalStorage('listData');
				let data = {
					id:new Date().getTime()+'',
					order:this.addForm.order,
					name: this.addForm.name,
					money: this.addForm.money,
					percent:this.addForm.percent,
					aftermoney:0,
				};
				list.push(data);
				this.$setlocalStorage('listData',list);
				this.addDialog = false;
				this.$message({
								message: '添加成功',
								type:'success'
						})
				setTimeout(()=>{
					this.get_data();
				},1000)
			},
			change(){
				let list = this.$getlocalStorage('listData')===''?[]:this.$getlocalStorage('listData');
				list = list.map(item=>{
					if(item.id===this.changeForm.id){
						item.order = this.changeForm.order;
						item.name = this.changeForm.name;
						item.money = this.changeForm.money;
						item.percent =this.changeForm.percent;
					}
					return item;
				})
				this.$setlocalStorage('listData',list);
				this.changeDialog = false;
				this.$message({
						message: '修改成功',
						type:'success'
				})
				setTimeout(()=>{
					this.get_data();
				},1000)
			},
			get_data() {
				this.loading = true;
				this.tableData = [];
				setTimeout(() => {
					let list = this.$getlocalStorage('listData')===''?[]:this.$getlocalStorage('listData');
					this.loading = false;
					this.tableData = list.map(item => {
						item.aftermoney = this.$accMul(item.money, item.percent).toFixed(2);
						return item;
					})
				}, 100)
			},
			handleEdit(row){
				this.changeForm = {
					id:row.id,
					order: row.order,
					name: row.name,
					money: row.money,
					percent:row.percent,
				};
				this.changeDialog = true;
			},
			handleDelete(row){
				this.deleteId = row.id;
				this.deleteDialog = true;
			},
			sureDelete(){
				let list = this.$getlocalStorage('listData')===''?[]:this.$getlocalStorage('listData');
				let index = list.findIndex(item=>item.id===this.deleteId);
				list.splice(index,1);
				this.$setlocalStorage('listData',list);
				this.deleteDialog = false;
				this.$message({
						message: '删除成功',
						type:'success'
				})
				setTimeout(()=>{
					this.get_data();
				},1000)
			},
			sureClear(){
				this.$removelocalStorage('listData');
				this.clearDialog = false;
				this.$message({
						message: '清除成功',
						type:'success'
				})
				setTimeout(()=>{
					this.get_data();
				},1000)
			},
		}
	}
</script>

<style lang="scss" scoped>
	.event-content {
		height: 70px;
		/*border-bottom: 1px solid #EBEEF5;*/
	}
	
	.event-btn-col {
		display: flex;
		align-items: center;
	}
	
	.el-header {
		padding: 0;
	}
	
	.top-bar {
		width: 100%;
		height: 100%;
		text-align: center;
		line-height: 60px;
		padding-right: 20px;
		box-sizing: border-box;
		background-color: #313952;
		color:yellow;
		box-shadow: 0px 5px 5px rgba(134, 143, 255, .1);
	}
	
	.nav-menu>div {
		color: #86939e;
		cursor: pointer;
	}
	
	.bell {
		font-size: 22px;
	}
	
	.nav-menu>div:hover {
		color: #03a9f4;
	}
	
	.nav-icon {
		font-size: 14px;
		color: #86939e;
	}
	
	.nav-title {
		font-size: 16px;
		color: #333333;
		margin-left: 10px;
	}
	
	.login-user {
		margin-left: 20px;
		font-size: 14px;
	}
	
	.exit {
		margin-left: 20px;
	}
	
	.fly-table-wrapper {
		border: 1px solid #ccc;
		background-color: #fff;
		padding: 0 20px;
	}
	
	.select-btn:hover {
		border-color: #409EFF;
		color: #409EFF;
	}
	
	.select-btn:nth-child(2) {
		margin-left: 20px;
	}
	
	.select-btn.active {
		border: 1px solid #409EFF;
		background-color: #409EFF;
		color: #fff;
	}
	
	.select-btn.active:hover {
		color: #fff;
		opacity: .8;
	}
	
	span.super {
		color: red;
		font-weight: 600;
		font-size: 16px;
	}
	
	span.heigh {
		color: orange;
		font-weight: 600;
		font-size: 16px;
	}
	
	span.middle {
		color: yellow;
		font-weight: 600;
		font-size: 16px;
	}
	
	span.low {
		color: blue;
		font-weight: 600;
		font-size: 16px;
	}
	
	span.normal {
		color: gray;
		font-weight: 600;
		font-size: 16px;
	}
	
	.detail-pop {
		box-sizing: border-box;
	}
	
	.detail-pop>div {
		margin-bottom: 10px;
	}
	
	.left-name {
		width: 90px;
		margin-right: 10px;
		text-align: right;
	}
	
	.kaiguan {
		font-size: 20px;
	}
	
	.second-view {
		width: 100%;
		height: auto;
		transition: all .3s cubic-bezier(.55, 0, .1, 1);
	}
	
</style>


